.header .container {
  max-width: 1475px;
  margin: 0 auto;
  padding: 0 25px;
}

.header__wrapper-column {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  padding-top: 32px;
}

.header__item {
  width: 37px;
  height: 12px;
  margin-left: 8.4%;

  border-top: 1px solid $white;
  border-bottom: 1px solid $white;
}

.header__contacts {
  margin-left: auto;
  margin-right: 2.8%;
}

.header__button {
  margin-right: 3px;
  padding: 12px 25px 15px 27px;

  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  color: $white;
  text-transform: uppercase;
  letter-spacing: 0.8px;

  border: 1px solid $white;
  border-radius: 5px;

  transition: all 0.3s;

  &:hover,
  &:focus {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

@media (max-width: $tablet-width) {
  .header__wrapper-column {
    padding-top: 20px;
  }

  .header__contacts {
    margin: 0;
    margin-bottom: 10px;
  }

  .header__item {
    margin: 0 36px 12px auto;
  }

  .header__button {
    display: none;
  }
}

@media (max-width: $mobile-width) {
  .header .container {
    padding: 0 16px;
  }

  .header__wrapper-column {
    padding-top: 17px;
  }

  .header__log {
    width: 38px;
    height: 38px;
  }

  .header__item {
    margin: 0 26px 5px auto;
  }

  .header__contacts {
    margin-bottom: 5px;
  }
}
